.ubtn,
.checkbox-ic {
  border-radius: 4px;
}
.ubtn-size-sm {
  border-radius: 2px;
}

.flight-item,
.modal-content {
  border-radius: 8px;
}
.ubtn-radio-wrap .ubtn {
  border: 1px solid transparent;
}
.ubtn.ubg-sub-primary,
.ubtn-radio-wrap > input:checked ~ .ubtn {
  background-color: transparent;
  border: 1px solid var(--color-primary);
}

.input-default ~ .input-frame,
.input-clean ~ .input-frame,
.input-material-2 ~ .input-frame,
.input-material-3 ~ .input-frame,
.dropdown-menu {
  border-radius: 0;
}

body {
  font: 100% "SVN-Gilroy", "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
  letter-spacing: 0;
}

input[type="radio"]:checked:not(:disabled) ~ .rnc-ic-wrap .radio-style-default.radio-ic {
  border: 1px solid var(--color-primary);
}

.radio-style-default:after {
  content: "";
  position: absolute;
  background: transparent;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  background: #005993;
  transition: 0.2s all ease-in-out;
  transform-origin: 0 -12.5%;
  transform: scale(0) translate(-50%, -50%);
}

input[type="radio"]:disabled .radio-style-default:after {
  display: none;
}

input[type="radio"]:checked:not(:disabled) ~ .rnc-ic-wrap .radio-style-default .radio-check,
input[type="radio"]:checked:not(:disabled)~ .rnc-ic-wrap .radio-style-default:after {
  transform: scale(1) translate(-50%, -50%);
}
